@import url('./variable.css');
.App {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header {
  background-color: var(--primary);
  height: 50px;
  color: var(--white);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  font-family: OpenSansBold;
  font-size: 16px;
}
.body {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: auto;
}
.bottom {
  height: 50px;
  color: var(--white);
  font-family: OpenSansBold;
  font-size: 16px;
  outline: none;
  border-color: var(--primary);
  background-color: var(--primary);
}

.enabledBtn {
  opacity: 1;
}
.disabledBtn {
  opacity: 0.3;
}


